.main{
    max-height: 50vh;
}

.mainContent{
    display:flex;
    padding-bottom: 20px;
    border-bottom: 1px solid #fff;
}

.punkHighlight{
    flex: 0.25;
    display: flex;
    align-items: center;
    justify-content: center;
}
.punkContainer{
    border-radius: 20px;
    overflow: hidden;
    display: flex;
    margin-right: 20px;
}
.selectedPunk{
    object-fit: contain;
    max-width: min(30vw,40vh);
    max-height: 30vw;
}

.punkDetails{
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between; 
    flex: 1;
}
.title{
    font-size: 96px;
    font-weight: 800;
    display: flex;
}
.itemNumber{
    color: #a1a5b0 ;
    font-size: 72px;
    align-self: flex-start;
    padding-top: 30px;
}
.owner{
    display: flex;
    margin: 10px 0;
    height: 50px;
    flex: 1;
    align-self: flex-end;
    justify-self: flex-start;
}
.ownerAddress{
    color: white;
}
.owner > div{
    margin: 0 5px;
}
.ownerImageContainer{
    height: 50px;
    width: 50px;
    border-radius: 50%;
    border-radius: 50px;
    overflow: hidden;
    object-fit: contain;
    color: white;
}
.ownerImageContainer > img{
    height: 100%;
    width: 100%;
}
.ownerDetails{
    display: flex;
    align-self: center;
    align-items:center;
    flex: 1;
}
.ownerNameAndHandle{
    flex: 1;
}
.ownerHandle{
    color: #00ebfe;
}
.ownerLink{
    padding: 12px;
    border-radius: 50px;
    height: 50px;
    width: 50px;
    border: 1px solid #fff;
    margin: 0 0 0 10px;
}
.ownerLink > img{
    height: 100%;
    width: 100%;
}

.imgPrice{
    color: white;
    align-self: center;
    font-size: 32px;
    font-weight: 400;
    
}